<template>
<v-container>
  <v-layout>
    <v-flex xs12 id="f1">
        <v-col cols="12" sm="12" md="12">
            <v-text-field
             ref="ev_name"
             v-model="ev_name"
             :rules="[() => !!ev_name || '행사이름을 입력해주세요.']"
             :eroor-messages="errorMessages"
             label="행사이름"
            ></v-text-field>
            <v-text-field
             ref="ev_url"
             v-model="ev_url"
             :rules="[() => !!ev_url || 'URL을 입력해주세요.']"
             :eroor-messages="errorMessages"
             label="홈페이지 URL"
            ></v-text-field>
            <v-menu
             v-model="menu"
             :close-on-content-click="false"
             transition="scale-transition"
             offset-y
             max-width="290px"
             min-width="290px"
            >
            <template v-slot:activator="{ on }">
                <v-text-field
                 v-model="dateRangeText"
                 :rules="[() => !!dateRangeText || '기간을 선택해주세요.']"
                 :eroor-messages="errorMessages"
                 label="기간"
                 persistent-hint
                 readonly
                 v-on="on"
                ></v-text-field>
            </template>
            <v-date-picker v-model="dates" range></v-date-picker>
            </v-menu>
            <v-file-input 
              v-model="ev_poster" accept="image/*" label="이미지 로드" @change="imgView">
            </v-file-input>
            <v-btn @click="insertEvent">test</v-btn>
            <div style="width:200px; height:300px;">
              <img :src="img"/>
            </div>
        </v-col>
    </v-flex>
  </v-layout>
</v-container>
</template>

<script>
  export default {
    data: () => ({
      date: new Date().toISOString().substr(0, 10),
      menu: false,
      dates:[],
      ev_name: '',
      ev_url: '',
      ev_poster: null,
      img: null,
    }),

    computed: {
      dateRangeText () {
        return this.dates.join(' ~ ')
      },
    },

    methods: {
      formatDate (date) {
        if (!date) return null

        const [year, month, day] = date.split('-')
        return `${month}/${day}/${year}`
      },
      parseDate (date) {
        if (!date) return null

        const [month, day, year] = date.split('/')
        return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
      },
      insertEvent(){
        if(this.ev_name === '' ||
        this.ev_url === '' ||
        this.date[0] === undefined ||
        this.ev_poster === undefined
        ){
          alert("빈칸 없이 입력해주세요.");
        }else{
          var item = new FormData()
          item.append('name', this.ev_name);
          item.append('url', this.ev_name);
          item.append('start_date', this.dates[0]);
          item.append('end_date', this.dates[1]);
          item.append('poster_path', this.ev_poster);

          // post 방식
          this.$http.post ('/api/event/insert', item).then(
            (res) => {
              // this.img = res.data;
              alert(res.data);
            },
            (err) => {
              alert("실패!!");
            })
            .catch(err => {
              alert(err);
            })

          // 시맨틱 url get방식
          // var name = this.ev_name;
          // this.$http.get ('/api/event/insert/'+document.getElementById("files").files[0])
          //   .then(
          //   (res) => {
          //     alert(res.data);
          //   },
          //   (err) => {
          //     alert("실패!!");
          //   })
          //   .catch(err => {
          //     alert(err);
          //   })
        }
      },
      imgView(){
        alert("메소드 연결!!")
        console.log(this.ev_poster);
        var img = new FormData();
        img.append('poster_img', this.ev_poster);
        this.$http.post ('/api/event/ftm-img', img).then(
        (res) => {
          this.img = res.data;
        },
        (err) => {
          alert("실패!!");
        }).catch(err => {
          alert(err);
        })
      }
    },
  }
</script>
